<template>
  <mavon-editor :toolbars="toolbars" v-model="value" :codeStyle="prop.codeStyle" :ishljs="true" :subfield="prop.subfield" :defaultOpen="prop.defaultOpen" :toolbarsFlag="prop.toolbarsFlag" :editable="prop.editable" :scrollStyle="prop.scrollStyle" :boxShadow="prop.boxShadow" ref=md />
</template>

<script>
export default {
  name: "markdown",
  data () {
    return {
      toolbars: {
        bold: true, // 
        italic: true, // 
        header: true, // 
        underline: true, // 
        strikethrough: true, // 
        mark: true, // 
        superscript: true, // 
        subscript: true, // 
        quote: true, // 
        ol: true, // 
        ul: true, // 
        link: true, // 
        imagelink: true, // 
        code: true, // code
        table: true, // 
        fullscreen: false, // 
        readmodel: false, // 
        htmlcode: true, // html
        help: true, // 
        /* 1.3.5 */
        undo: true, // 
        redo: true, // 
        trash: true, // 
        save: false, // eventssave
        /* 1.4.2 */
        navigation: true, // 
        /* 2.1.8 */
        alignleft: true, // 
        aligncenter: true, // 
        alignright: true, // 
        /* 2.2.1 */
        subfield: true, // 
        preview: false // 
      },

      value: "# \n\n![fengtaishishu.jpeg](http://192.168.1.118:8088/images/6778573061923934209.png)}```"
    };
  },
  methods: {
  },
  computed: {
    prop() {
      let data = {
        // 
        subfield: false,
        // edit: , preview
        defaultOpen: 'preview',
        editable: false,
        toolbarsFlag: false,
        scrollStyle: false,
        // 
        boxShadow: false,
        codeStyle:'googlecode',//
      }
      return data
    }
  }
}
</script>

<style scoped>
</style>



